.product{
    width: 200px;
    height: 250px;
    transition: 0.7s;
}
.product:hover{
    transform: scale(1.19);
    .details{
        background-color: rgb(5, 188, 188);
    }
    .details p{
        color:red;
    }
}
.product .img{
    width:100%;
    height:150px;
}
.product .img img{
    width:100%;
    height: 100%;
    object-fit: cover;
    
}
.details{
    padding: 5px;
    background-color: teal;
    transition: 0.5s;
}

.details h3{
    margin-bottom: 10px;
    font-size: 14px;
}
.details button{
    margin-top:10px;
    padding: 5px 10px;
    font-weight: 500;
    background-color:#FFD700;
    transition: 0.5s;
    border-radius: 3px;
}
.details button:hover{
    background-color: #c5a803;
    border-color: rgb(1, 1, 1);
    border-radius: 3px;
}
.details p{
    font-size: 14px;
    font-weight: 500;
    color:greenyellow;
    transition: 0.5s;
}
.rmv{
    background-color: #cf3d3d !important;
}