.navbar{
    width:100%;
    height: 50px;
    background-color: #222;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}
.logo{
color: #fff;
font-size: 20px;
}
.navbar ul{
    display: inline-flex;
    gap: 10px;
}
.navbar ul li{
    list-style-type: none;
}
.navbar ul li a{
    color:#fff;
    text-decoration: none;
}
.cart-count{
    padding: 5px;
    background-color: yellowgreen;
    height: 20px;
    width: 20px;
    display: inline-block;
    border-radius: 50%;
    font-size: 12px;
    text-align: center;
    color: #222;
    font-weight: 600;
}